        :key="idx"
        :question="q.default"
      />
    </div>
  </div>
</template>

<script>
import questions from './get-questions'
import Question from './components/question'

export default {
  components: {
    Question
  },
  data() {
    return {
      questions
    }
  },
  computed: {
    sortedQuestion() {
      return this.questions
    }
  }
}
</script>
