<template>
  <div class="c-question">
    <div class="question-info">
      <div class="top-row row" v-if="!!level || (tags && tags.length)">
        <div class="level-info" v-if="!!level">
          <div class="title">难度</div>
          <div class="content">{{ level }}</div>
        </div>
        <div class="tag-ctn" v-if="tags && tags.length">
          <div class="title">标签</div>
          <div class="content">
            <ul class="tag-list">
              <li class="tag-item" v-for="(t, idx) in tags" :key="idx">
                <span>{{ t.value }}</span>
              </li>
            </ul>
          </div>
        </div>
      </div>
      <div class="question-description row">
        <div class="title">题目</div>
        <div class="content">{{ description }}</div>
      </div>
      <div class="question-params row">
        <div class="title">输入</div>
        <div class="content">
          <table>
            <thead>
              <tr>
                <th>参数名</th>
                <th>类型</th>
                <th>描述</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="(p, idx) in params" :key="idx">
                <td>{{ p.name || 'undefined' }}</td>
                <td>
                  {{ p.type && p.type.names ? p.type.names.join(',') : 'any' }}
                </td>
                <td>{{ p.description || 'undefined' }}</td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
      <div class="question-returns row">
        <div class="title">输出</div>
        <div class="content">
          <table>
            <thead>
              <tr>
                <th>类型</th>
                <th>描述</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="(r, idx) in returns" :key="idx">
                <td>
                  {{ r.type && r.type.names ? r.type.names.join(',') : 'any' }}
                </td>
                <td>{{ r.description || 'undefined' }}</td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>
    <div class="code row">
      <div class="title">代码</div>
      <div class="content">
        <Codemirror v-if="openCode" :value="question.code" />
        <a
          class="toggle-code-btn"
          href=""
          @click.prevent="openCode = !openCode"
          >{{ openCode ? `收起代码 ↑` : '查看代码 ↓' }}</a
        >
      </div>
    </div>
  </div>
</template>

<script>
import Codemirror from './codemirror'
export default {
  components: {
    Codemirror
  },
  props: {
    question: {}
  },
  data() {
    return {
      openCode: false
    }
  },
  computed: {
    description() {
      return this.question.comment.description
    },
    params() {
      return this.question.comment.params
    },
    returns() {
      return this.question.comment.returns
    },
    level() {
      let tags = this.question.comment.tags || []
      let levelTag = tags.find(i => i.title === 'level')
      return levelTag ? levelTag.value : null
    },
    tags() {
      let tags = this.question.comment.tags || []
      return tags.filter(i => i.title === 'tag')
    }
  }
}
</script>

<style lang="css" scoped>
.c-question {
  margin-bottom: 20px;
}
.row {
  margin-bottom: 8px;
  display: flex;
  align-items: flex-start;
  line-height: 1.5;
}
.content {
  flex-grow: 1;
}

table {
  width: 100%;
}

.title {
  flex-shrink: 0;
  margin-right: 8px;
  font-weight: bold;
  color: #666;
}

th {
  font-weight: bold;
}

td,
th {
  min-width: 90px;
  padding: 5px;
  border: 1px solid #ccc;
}

td:not(:first-child),
th:not(:first-child) {
  border-left: none;
}

thead tr:last-child th {
  border-bottom: none;
}

tbody tr:not(:first-child) td {
  border-top: none;
}

.question-params .title,
.question-returns .title {
  padding: 5px 0;
}

.toggle-code-btn {
  color: #00965e;
  display: block;
  text-align: center;
  text-decoration: none;
}

.toggle-code-btn:hover {
  color: #004a2e;
}

.toggle-code-btn:visited {
  color: #00965e;
}
</style>
