        comment: {"comment":"/**\r\n * @description 有这样一道智力题：“某商店规定：三个空汽水瓶可以换一瓶汽水。小张手上有十个空汽水瓶，她最多可以换多少瓶汽水喝？”答案是5瓶，方法如下：先用9个空瓶子换3瓶汽水，喝掉3瓶满的，喝完以后4个空瓶子，用3个再换一瓶，喝掉这瓶满的，这时候剩2个空瓶子。然后你让老板先借给你一瓶汽水，喝掉这瓶满的，喝完以后用3个空瓶子换一瓶满的还给老板。如果小张手上有n个空汽水瓶，最多可以换多少瓶汽水喝？\r\n * @param n {Number} 小张初始拥有的饮料瓶数量\r\n * @returns {Number} 小张能喝到的饮料数量\r\n */","meta":{"range":[286,514],"filename":"oaaq0evv73s0s5y0cu9c3ho.js","lineno":6,"columnno":0,"path":"C:\\Users\\ADMINI~1\\AppData\\Local\\Temp","code":{"id":"astnode100000002","name":"calcDrinks","type":"FunctionDeclaration","paramnames":["n"]},"vars":{"drinks":"calcDrinks~drinks","lasts":"calcDrinks~lasts"}},"description":"有这样一道智力题：“某商店规定：三个空汽水瓶可以换一瓶汽水。小张手上有十个空汽水瓶，她最多可以换多少瓶汽水喝？”答案是5瓶，方法如下：先用9个空瓶子换3瓶汽水，喝掉3瓶满的，喝完以后4个空瓶子，用3个再换一瓶，喝掉这瓶满的，这时候剩2个空瓶子。然后你让老板先借给你一瓶汽水，喝掉这瓶满的，喝完以后用3个空瓶子换一瓶满的还给老板。如果小张手上有n个空汽水瓶，最多可以换多少瓶汽水喝？","params":[{"type":{"names":["Number"]},"description":"小张初始拥有的饮料瓶数量","name":"n"}],"returns":[{"type":{"names":["Number"]},"description":"小张能喝到的饮料数量"}],"name":"calcDrinks","longname":"calcDrinks","kind":"function","scope":"global"},
